@import url("../app.css");
$box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1), 0 10px 20px rgba(0, 0, 0, 0.06);

.main {
  font-family: "GmarketSansMedium", serif;
  width: 100%;
  height: 92vh;
  display: grid;
  grid-template-columns: minmax(200px, 1fr) 4fr;
  box-sizing: border-box;
  overflow: hidden;
  .left_SideBars {
    border-right: 2px solid black;
  }
  .right_Sidebars {
    border-left: 2px solid black;
  }
}

.left_SideBars {
  display: grid;
  grid-auto-rows: 70px;
  row-gap: 3px;
  overflow: auto;
  &::-webkit-scrollbar {
    width: 10px;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #2f3542;
  }
  &::-webkit-scrollbar-track {
    border-radius: 10px;
    background-color: gray;
    box-shadow: inset 0 0 5px white;
  }
  .character {
    margin-top: 8px;
    font-family: "GmarketSansMedium", serif;
    justify-self: center;
    display: flex;
    align-items: center;
    border: 1px solid black;
    border-radius: 15px;
    width: 90%;
    box-shadow: $box-shadow;
    position: relative;
  }
}
.job_link {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: black;
}
.job_delete_btn {
  background-color: inherit;
  border: none;
  position: absolute;
  right: 0;
}
.job_delete_modal {
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  z-index: 2;
  backdrop-filter: blur(20px);
  display: flex;
  align-items: center;
  justify-content: center;
}
.job_delete_modal_div {
  padding: 2rem;
  width: 35%;
  height: 35%;
  font-size: 1.7rem;
  background-color: #ecf0f1;
  box-shadow: $box-shadow;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  div {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-top: 10rem;

    button {
      font-size: 1.4rem;
      border-radius: 10px;
      width: 8rem;
      height: 4rem;
      padding: 0;
      background-color: inherit;
      border: 1px solid black;
    }
  }
}
.home {
  display: flex;
  flex-direction: column;
  padding: 10px;
}
.time h1 {
  font-size: 3rem;
  text-align: center;
  margin-top: 20%;
}
.link {
  display: flex;
  justify-content: space-around;
  margin-top: 30%;
  font-size: 1.2rem;
  .page_link {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 33%;
    a {
      display: flex;
      justify-content: center;
    }
  }
}

.dark_mode {
  background-color: #2c3e50;
  color: #ecf0f1;
  a {
    color: #ecf0f1;
  }
}
.dark_mode_job {
  background-color: #7696c0;
  color: #ecf0f1;
  a {
    color: #ecf0f1;
  }
}
.light_mode_job {
  background-color: white;
}
.dark_mode_btn {
  background-color: #bdc3c7;
  color: black;
}
.dark_mode_area {
  background-color: #487eb0;
}
.light_mode_area {
  background-color: #74b9ff;
}
